
ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}
.wrap {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  padding: 20px 0;
}
@media (min-width: 1200px){
.datepicker,.lang {
      width: 1170px;
}
}
@media (min-width: 992px){
.datepicker,.lang {
      width: 1170px;
}
}
@media (min-width: 768px){
.datepicker,.lang {
      width: 1170px;
}
}
.langBox{
  margin-bottom: 10px;
}
.datepicker{
  line-height: 32px;
  padding: 0 5px;
  margin: 0 auto;
}
.lang{
  overflow: hidden;
  margin: 0 auto;
}
.lang li{
  float: left;
  cursor: pointer;
  padding: 2px 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin: 0 5px 5px;
}
.lang li:hover{
  border-color: #5bc0de;
  color: #5bc0de;
}
.fade-enter, .fade-leave-active{
  opacity: 0;
  -webkit-transform: scale3d(0, 0, 0);
          transform: scale3d(0, 0, 0);
}
.fade-enter-active, .fade-leave-active{
  transition: all ease .1s;
}
.loadding{
  position: fixed;
}
.loaddingBox{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,.4);
}
.loadding span{
  position: fixed;
  top: 50%;
  left: 50%;
  margin-top: -50px;
  margin-left: -50px;
  font-size: 18px;
  width: 100px;
  height: 100px;
  text-align: center;
  line-height: 100px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,.5);
  background-color: #fff;
}



